/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import { Box, Grommet, Button } from 'grommet'

import { deepFreeze } from "grommet/utils"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Box
        tag='header'
        direction='row'
        align='left'
        justify='between'
        background='light-2'
        height='xxsmall'
        elevation='medium'
      >
        <h4>
          {data.site.siteMetadata?.title.toLowerCase() || `Title`}
        </h4>
        <Box direction='row' pad='small' gap='small'>
          <Button size='small' color='brand' label="dashboard" />
          <Button size='small' color='brand' label="stats" />
          <Button size='small' color='brand' label="about" />
        </Box>
      </Box>
      <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `0 1.0875rem 1.45rem`,
        }}
      >
        <main>{children}</main>
        <footer
          style={{
            marginTop: `2rem`,
          }}
        >
          © {new Date().getFullYear()}, Built with
          {` `}
          <a href="https://www.gatsbyjs.com">Gatsby</a>
        </footer>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
